<script lang="ts">import { onMount } from "svelte";
import Requests from "../components/Requests.svelte";
import Welcome from "../components/Welcome.svelte";
import RequestsPerHour from "../components/RequestsPerHour.svelte";
import ResponseTimes from "../components/ResponseTimes.svelte";
import Endpoints from "../components/Endpoints.svelte";
import Footer from "../components/Footer.svelte";
import SuccessRate from "../components/SuccessRate.svelte";
import Activity from "../components/Activity.svelte";
import Version from "../components/Version.svelte";
import UsageTime from "../components/UsageTime.svelte";
import Growth from "../components/Growth.svelte";
import Device from "../components/Device.svelte";
function formatUUID(userID) {
    return `${userID.slice(0, 8)}-${userID.slice(8, 12)}-${userID.slice(12, 16)}-${userID.slice(16, 20)}-${userID.slice(20)}`;
}
async function fetchData() {
    userID = formatUUID(userID);
    // Fetch page ID
    try {
        const response = await fetch(`https://api-analytics-server.vercel.app/api/user-data/${userID}`);
        if (response.status == 200) {
            const json = await response.json();
            data = json.value;
            console.log(data);
            setPeriod('month');
        }
    }
    catch (e) {
        failed = true;
    }
}
function daysAgo(date, days) {
    let monthAgo = new Date();
    monthAgo.setDate(monthAgo.getDate() - days);
    return date > monthAgo;
}
function allTimePeriod(date) {
    return true;
}
function periodToDays(period) {
    if (period == '24-hours') {
        return 1;
    }
    else if (period == 'week') {
        return 8;
    }
    else if (period == 'month') {
        return 30;
    }
    else if (period == '3-months') {
        return 30 * 3;
    }
    else if (period == '6-months') {
        return 30 * 6;
    }
    else if (period == 'year') {
        return 365;
    }
    else {
        return null;
    }
}
function setPeriod(value) {
    period = value;
    let days = periodToDays(period);
    let inPeriod = allTimePeriod;
    if (days != null) {
        inPeriod = (date) => { return daysAgo(date, days); };
    }
    let dataSubset = [];
    for (let i = 0; i < data.length; i++) {
        let date = new Date(data[i].created_at);
        if (inPeriod(date)) {
            dataSubset.push(data[i]);
        }
    }
    periodData = dataSubset;
}
let data;
let periodData;
let period = 'month';
let failed = false;
onMount(() => {
    fetchData();
});
export let userID;
</script>

{#if periodData != undefined}
  <div class="dashboard">
    <div class="time-period">
      <button class="time-period-btn {period == '24-hours' ? 'time-period-btn-active' : ''}" on:click="{() => {setPeriod('24-hours')}}">
        24 Hours
      </button>
      <button class="time-period-btn {period == 'week' ? 'time-period-btn-active' : ''}" on:click="{() => {setPeriod('week')}}">
        Week
      </button>
      <button class="time-period-btn {period == 'month' ? 'time-period-btn-active' : ''}" on:click="{() => {setPeriod('month')}}">
        Month
      </button>
      <button class="time-period-btn {period == '3-months' ? 'time-period-btn-active' : ''}" on:click="{() => {setPeriod('3-months')}}">
        3 Months
      </button>
      <button class="time-period-btn {period == '6-months' ? 'time-period-btn-active' : ''}" on:click="{() => {setPeriod('6-months')}}">
        6 Months
      </button>
      <button class="time-period-btn {period == 'year' ? 'time-period-btn-active' : ''}" on:click="{() => {setPeriod('year')}}">
        Year
      </button>
      <button class="time-period-btn {period == 'all-time' ? 'time-period-btn-active' : ''}" on:click="{() => {setPeriod('all-time')}}">
        All time
      </button>
    </div>
    <div class="left">
      <div class="row">
        <Welcome />
        <SuccessRate data={periodData} />
      </div>
      <div class="row">
        <Requests data={periodData} />
        <RequestsPerHour data={periodData} />
      </div>
      <ResponseTimes data={periodData} />
      <Endpoints data={periodData} />
      <Version data={periodData} />
    </div>
    <div class="right">
      <Activity data={periodData} {period} />
      <div class="grid-row">
        <Growth data={periodData} />
        <Device data={periodData} />
      </div>
      <UsageTime data={periodData} />
    </div>
  </div>
{:else if failed}
  <div class="no-requests">No requests currently logged.</div>
{:else}
  <div class="placeholder" style="min-height: 85vh;">
    <div class="spinner">
      <div class="loader"/>
    </div>

  </div>
{/if}
<Footer />

<style>
  .dashboard {
    min-height: 90vh;
  }
  .dashboard {
    margin: 5em;
    display: flex;
    position: relative;
  }
  .row {
    display: flex;
  }
  .grid-row {
    display: flex;
  }
  .right {
    flex-grow: 1;
    margin-right: 2em;
  }
  .no-requests {
    height: 70vh;
    font-size: 1.5em;
    display: grid;
    place-items: center;
    color: var(--highlight);
  }
  .placeholder {
    min-height: 85vh;
    display: grid;
    place-items: center;
  }
  .time-period {
    position: absolute;
    display: flex;
    right: 2em;
    top: -2em;
  }
  .time-period-btn {
    background: #232323;
    padding: 2px 8px;
    margin-left: 6px;
    border-radius: 3px;
    border: 1px solid #2E2E2E;
    color: #707070;
    cursor: pointer;
  }
  .time-period-btn-active {
    background: var(--highlight);
    color: black;
  }
  @media screen and (max-width: 1580px){
    .grid-row {
        flex-direction: column;
      }
  }
  @media screen and (max-width: 1100px) {
    .dashboard {
      margin: 2em 0;
    }
  }

</style>
